#article-info-container {
    display: flex;
    justify-content: center;
    min-width: 800px;
    position: relative;
    flex-wrap: wrap;
}

#article-info-container a {
    color: rgb(0, 0, 0);
    text-decoration: none;
    cursor: pointer;
}

#article-info-container a::before {
    content: "> ";
    text-decoration: none;
}

#article-info-container a:visited {
    color: rgb(0, 0, 0);
}

#article-info-container a:active {
    color: rgb(0, 0, 0);
}

#article-info-container > div {
    width: 40%;
    min-height: 140px;
    margin: 40px 0;
    background-color: lightgray;
}

#article-info-container img {
    position: relative;
    width: 100%;
    z-index: 200;
    display: block;
    margin: -20px 0;
    min-height: 180px;
    min-width: 320px;
    border: 5px solid gray;
}

#article-info-container .info {
    position: relative;
    min-height: 80px;
    text-align: left;
    background-color: lightgray;
}

#article-info-container .first{

    font-size: 2em;
}

#article-info-container .first a::before{
    content:none;
}

#article-info-container .date{
position: relative;
    margin-top: 20px;
}

#article-info-container .info table {
    margin-top: 15px;
    padding-left: 15px;
}

#article-info-container .info .date {
    position: absolute;
    right: 20px;
}

#article-info-container .info div {
    position: absolute;
    bottom: 20px;
    right: 20px;
}

@media (max-width: 768px) {
    #article-info-container {
        flex-direction: column;
        align-items: center;
        min-width: unset;
    }

    #article-info-container > div {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 90%;
        margin: 20px 0;
    }

    #article-info-container img {
        width: 100%;
    }

    #article-info-container .info {
        margin-top: -15px;
        text-align: center;
        padding: 15px 0;
    }

    #article-info-container .info table {
        margin: 10px auto;
        padding-left: 0;
    }

    #article-info-container .info .first {
        text-align: left;
        padding-left: 20px;
    }

    #article-info-container .info .date {
        /* position: static; */
        /* margin-top: 12px; */
    }

    #article-info-container .info .text-flow {
        /* position: static; */
        padding: 0 20px;
        text-align: left;
    }

    #article-info-container .info div {
        position: static;
        margin-top: 10px;
    }
}
