#flag {
    font-size: 5em;
}

#page-content-wrapper {
    display: flex;
    justify-content: flex-start; 
    flex-wrap: wrap;
    width: 90%;
    margin: auto;
    gap: 4px;
}

#page-content-wrapper > div {
    flex: 0 0 calc(50% - 2px);
    box-sizing: border-box;
    padding: 20px;
}

#page-content-wrapper .spot:nth-last-child(1):nth-child(odd) {
    flex: 0 0 calc(50% - 2px);
    margin-left: 0;
}


#page-content-wrapper #description {
    text-align: left;
    padding: 20px 20px 20px 0;
    line-height: 2em;
    
}

#page-content-wrapper #terrain img {
    width: 100%;
}

#page-content-wrapper #terrain .svgObject {
    width: 100%;
}

#page-content-wrapper #between-child{
    flex: 0 0 100%; 
}

#page-content-wrapper .spot{
    padding: 0;
    text-align: left;
}

#page-content-wrapper .spot img {
    width: 100%;
    /* min-width: 50%; */
    height: 300px;
    object-position: center;
    object-fit: cover;
}

#page-content-video iframe {
    margin-top: 40px;
    position: relative;
    width: 90%;
    aspect-ratio: 16 / 9;
    border: none;
}

@media (max-width: 768px) {
    #page-content-wrapper > div {
        flex: 0 0 100%;
    }


#page-content-wrapper .spot img {
    min-width: 400px;
    object-position: center;
}

    #page-content-video > iframe {
        width: 90%;
        aspect-ratio: 16 / 9;
    }
}
