html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}

hr {
  /* height: 1px; */
  /* border: 0; */
  border-top: 1px solid #ccc;
  margin: 20px 0;
}

.bold{
  font-weight: bold;
}

.close-btn {
    cursor: pointer;
    /* color: black; */
    margin-bottom: 20px;
}

.text-flow{
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;

}

.aspect-ratio {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 56.25%; /* The height of the item will now be 56.25% of the width. */
}

#hamburger-btn {
  position: absolute;
  top: 5px;
  right: 20px;
  /* color: white; */
  cursor: pointer;
  z-index: 1;
  font-size: 2em;
  font-weight: 900; 
}