#title-wrapper{
    /* background-color: aqua; */
    width: 100%;
    height: 50%;
    margin: 0 auto; 
}

#title{
    position: relative;
    width: 100%;
    height: 50%;
    margin: 0 auto; 
    padding: 20px;
    box-sizing: border-box;
}

#title #txt{
    /* width:100%; */
    position: relative;
    width:100%;

}

#title .graphic1{
    position: absolute;
    left: 56%;
    top: 3%;
    width: 6vw;
    height: 6vh;
    max-width: 115px;
    max-height: 94px;
    /* background-color: blue; */
}

.intro{
    font-weight: bold;
    text-align: center;
    padding: 20px;
}

@media (max-width: 768px) {
    .intro{
        text-align: left;
    }
}