#menu{
    position: absolute;
    top: 0;
    right: 0%;
    width: 300px;
    background-color: #000000aa;
    height: 100%;
    padding: 20px;
    text-align: left;
    /* display: inline-block; */
    z-index: 10000;
    color: beige;
}

#menu a {
    color: white;
    text-decoration: none;
  }

#menu a:visited {
    color: white;
  }

#menu a:active {
    color: white;
  }

#menu .close-btn{
    text-align: right;
    font-size: 2em;
    margin-top: -15px;
}

#menu .material-icons{
    display: inherit;
}

#menu ul{
    list-style-type: none;
    margin: 0;
    padding: 0px 20px;
    font-size: larger;
    font-weight: bolder;
}

#menu li{
    padding: 10px 0;
}